import React, { Component } from 'react'
import ToDo from './Todo'
import NewToDoForm from './newtodoform.js'

class ToDoList extends Component {
  constructor(props){
    super(props);
    this.state = {todo: []};

    this.add = this.add.bind(this);
    this.remove = this.remove.bind(this);
    this.edit = this.edit.bind(this);
    this.toggleItem = this.toggleItem.bind(this);
  }

  add (item){
    
   this.setState({todo: [...this.state.todo, item]})
   console.log(this.state.todo);
  }

  remove(id){
    console.log(id)
    this.setState({
      todo: this.state.todo.filter(i => i.id !== id )
    });
  }

  edit(task, id){
    const updatedToDo = this.state.todo.map (item => {if (item.id === id){return {...item, item:task}}return item;});
    this.setState({todo: updatedToDo});
    console.log(updatedToDo)
  }

  toggleItem(id){
    const updatedToDo = this.state.todo.map (item => {if (item.id === id){return {...item, completed:(!item.completed)}}return item});
    console.log(updatedToDo)
    this.setState({todo: updatedToDo});
  }

  render(){
    const TODO = this.state.todo.map(item=><ToDo thing={item.item} key= {item.id} id={item.id} complete={item.completed} removeItem={this.remove} editItem={this.edit} toggleItem={this.toggleItem} />)
    return(
      <div className="ToDoList">
        <h1>Things to do</h1>
        <ul>{TODO}</ul>
        <NewToDoForm addToDo={this.add} />
      </div>
    );
  }
}

export default ToDoList;