import React, { Component } from 'react'

class ToDo extends Component {
  constructor(props){
    super(props);
    this.state ={task: this.props.thing, editing: false, activated: false}
    this.handleClick =this.handleClick.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this)
    this.handleToggle = this.handleToggle.bind(this);
  }

  handleClick(e){
    e.preventDefault();
    
    if (e.target.name === "close"){this.props.removeItem(this.props.id);}
    else if (e.target.name === "edit"){this.setState({editing: true});}
  }
  
  handleToggle(e){
    e.preventDefault();
    this.props.toggleItem(this.props.id);
    this.setState({activated: true})
  }

  handleChange (e){
     this.setState({[e.target.name]: e.target.value}) 
  }

  handleSubmit(e){
    e.preventDefault();
    this.props.editItem(this.state.task, this.props.id);
    this.setState({editing: false})
  }

  render(){
    let display;
    if (this.state.editing){
      display =(
      <div className="ToDo"> 
      <form className="todo-edit-form" onSubmit={this.handleSubmit}>
        <input type="text" name="task" value={this.state.task} onChange={this.handleChange}/>
        <button name="save">+</button>
      </form>
      </div>
      );
    }
    else{
      let cname = (this.props.complete) ? "ToDo-Task Completed" : "ToDo-Task" 
      if (this.state.activated && !this.props.complete){cname = cname + " Activated"}     
      display = 
        <div className="ToDo">
          <li className={cname} name="todoItem" onClick={this.handleToggle}>{this.props.thing}</li>
          <div className="ToDo-Btn-Group">
            <button onClick={this.handleClick} name="edit">Edit</button>
            <button onClick={this.handleClick} name="close">X</button>
          </div>
        </div>
    }
    return(
      <div>
        {display}
      </div>
    );
  }
}

export default ToDo;